import { Component, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { FamilyDetails } from '../Models/familyDetails';
import { ApiService } from '../services/api.service';
// import { ApiService } from 'src/src/app/services/api.service';
import { LanguageService } from '../services/language.service';

@Component({
  selector: 'app-edit-family-details',
  templateUrl: './edit-family-details.page.html',
  styleUrls: ['./edit-family-details.page.scss'],
})
export class EditFamilyDetailsPage implements OnInit {
  submitted: boolean = false;
  familyFrom: any;
  CHILD_NAME: any = ''
  showDynamicChilds: boolean = false;
  numberValue: any[] = ["1", "2", "3", "4", "5"];
  allChildName: any[] = [];
  allChildAge: any[] = [];
  allChildValue: any[] = [];
  familyData: any = {}
  finalValue: any[] = [];
  constructor(private languageService: LanguageService, private apiService: ApiService, private fb: FormBuilder) { }

  form = new FormGroup({
    userId: new  FormControl(''),
    FATHER_NAME: new FormControl('', [Validators.required]),
    FATHER_AGE: new FormControl('', [Validators.required]),
    MOTHER_AGE: new FormControl('', [Validators.required]),
    MOTHER_NAME: new FormControl('', [Validators.required]),
    WIFE_AGE: new FormControl('', [Validators.required]),
    WIFE_NAME: new FormControl('', [Validators.required]),
    CHILD_DETAIL: new FormArray([]),
    // CHILD_NAME: new FormControl('', [Validators.required]),
  })
  ngOnInit() {
    this.languageService._translateLanguage();
    this.familyFrom = this.fb.group({
      dataValue: [''],
      childValue: this.fb.array([this.createItems()]),
    });

    this.getFamily();
  }
  

  getFamily(){
    this.apiService.getFamilyDetailsById(localStorage.getItem('userId'))
      .subscribe((res: any) => {
        console.log(res);
        // this.familyData = res.data;
        // console.log(this.familyData)
        res.data.CHILD_DETAIL.forEach((data: any) => {
          (this.familyFrom.get("childValue") as FormArray).push(data)
        })
        
        console.log(this.familyFrom)
      }, (err => { console.log(err)}));
  }

  f(controls) {
    return this.form.get(controls);
  }
  

  createItems(): FormGroup {
    return this.fb.group({
      CHILD_NAME: '',
      CHILD_AGE: '',
    });
  }

  addChilds(){
    let textBox = this.familyFrom.get('dataValue').value;
     if(textBox.length === 1){
      console.log(textBox);
      this.showDynamicChilds = true;
     }
     this.childDataValue.clear();
    for (let i = 1; i <=textBox; i++) {
      this.showDynamicChilds = true;
     this.childDataValue.push(this.createItems());
     console.log(this.childDataValue.controls);
    }

  }

  get childDataValue() {
    return this.familyFrom.get('childValue') as FormArray;
  }

  getChildName(childName){
    console.log(childName);
    this.allChildName = [];
      this.allChildName.push(childName);

  }

  getChildAge(chidAge){
    console.log(chidAge)
    this.allChildAge = [];
    this.allChildAge.push(chidAge);
    // this.childDataValue.value.forEach((data: any) => {
    //   this.allChildAge.push(data);
    // })
  }


  onSubmit(){
    this.submitted = true;
    // if(this.form.invalid){
    //   return;pap
    // } 
    console.log(this.allChildName);
    console.log(this.allChildAge);
    this.allChildName.forEach((data: any) => {
      this.finalValue.push(data)
    })
    this.allChildAge.forEach((data: any) => {
      this.finalValue.push(data)
    })

    console.log(this.finalValue)
    console.log(this.familyFrom.value)
    // console.log(this.allChildValue);
    // this.form.value.userId = localStorage.getItem('userId');
    // this.form.value.CHILD_DETAIL = this.allChildValue;
    // // this.apiService.saveFamilyDetails(this.form.value);
  }

}
